.App {
  align-items: center;
  display: flex;
  justify-content: center;
  min-height: 100vh;
}

.MuiTypography-root.graph-title {
  margin-top: 1em;
  font-size: 0.8rem;
  color: #666;
}

text {
  font-family: 'Roboto' , sans-serif;
}

line, path {
  stroke: #777;
  stroke-width: 0.5;
}

text {
  fill: #666
}

.xAxis .tick line {
  opacity: 0;
}

.bar {
  cursor: pointer;
  fill:#3f51b5;
  transition: fill .1s ease;
}

.bar:hover{
  fill:#ff9800;
}

.axis-label {
  font-size: 0.8rem;
}

.gridCircle {
  fill: none;
  stroke: #CDCDCD
}

.gridLabel {
  font-size: 10px;
  fill: #666;
}

.axisLine {
  stroke: #CDCDCD;
  stroke-width: 0.5px;
}

.legend {
  font-size: 11px;
  text-anchor: middle;
}

.radarStroke {
  stroke-width: 1px;
  stroke:#3f51b5;
  fill: #3f51b5;
  fill-opacity: 0.2;
  transition: fill-opacity .2s ease;
}

.radarStroke:hover {
  fill-opacity: 0.4;
}

.radarCircle {
  fill: #3f51b5;
}

.radarCircle:hover {
  fill: #ff9800;
  cursor: pointer;
}